<template>
	<div>
		<Menu :titulo="titulo" />
		<div>
			<div @click="cambiarDisposicion" style="cursor:pointer; margin-left: 5%; width: 200px">
				<font-awesome-icon icon="th" style="font-size: 1.5em;"/>
				<div style="display:inline;height:40px;line-height:40px; margin-left: 5px;">Cambiar disposición</div>
			</div>
		</div>
		<CamarasRT1 v-if='disposicion==1'/>
		<CamarasRT2 v-if='disposicion==2'/>
		<CamarasRT3 v-if='disposicion==3'/>
		<CamarasRT4 v-if='disposicion==4'/>
		
		<Aviso/>
		<Grabar/>
	</div>
</template>

<script>
import Menu from "@/components/Menu";
import Aviso from "@/components/Aviso";
import Grabar from "@/components/BotonGrabar";
import CamarasRT1 from "@/components/CamarasRT1";
import CamarasRT2 from "@/components/CamarasRT2";
import CamarasRT3 from "@/components/CamarasRT3";
import CamarasRT4 from "@/components/CamarasRT4";

export default {
	name: "TiempoReal",
	components: {
		Menu,
		CamarasRT1,
		CamarasRT2,
		CamarasRT3,
		CamarasRT4,
		Aviso,
		Grabar
	},
	data() {
		return {
		titulo: "TIEMPO REAL",
		disposicion: 1
		};
	},
	methods: {
		/**
		 * Cambiar la disposicion de las camaras
		 */
		cambiarDisposicion(){
			if(this.disposicion == 1){
				this.disposicion = 2;
			}
			else if(this.disposicion == 2){
				this.disposicion = 3;
			}
			else if(this.disposicion == 3){
				this.disposicion = 4;
			}
			else {
				this.disposicion = 1;
			}
		}
	}
};
</script>

