<template>
  <div>
    <Menu :titulo="titulo"/>

     <br>
     <div style="text-align:center;"><h3>Bienvenido a la plataforma web del proyecto acción. Aquí deberá iniciar sesión para poder
         acceder al control de su sistema de cámaras de videovigilancia</h3></div>
        <form action :class="{cuadroLogin: !this.$vuetify.theme.dark, cuadroLoginOscuro: this.$vuetify.theme.dark}">
            <label :class="{form_label: !this.$vuetify.theme.dark, form_label_oscuro: this.$vuetify.theme.dark}" for="#email">Correo electrónico:</label>
            <input :class="{form_input: !this.$vuetify.theme.dark, form_input_oscuro: this.$vuetify.theme.dark}" type="email" id="email" required placeholder="usuario@dominio.com">
            <label :class="{form_label: !this.$vuetify.theme.dark, form_label_oscuro: this.$vuetify.theme.dark}" for="#password">Contraseña:</label>
            <input :class="{form_input: !this.$vuetify.theme.dark, form_input_oscuro: this.$vuetify.theme.dark}" type="password" id="password" required placeholder="Contraseña">
            <br>
            <input type="submit" value="¿Has olvidado tu contraseña?" style="color: #197BF6;" v-on:click="recuperarContrasena()">
            <input class="form-submit" type="submit" value="Login" v-on:click="inicioSesion()">
        </form>
  </div>
</template>

<script>
import ActivaCamaras from "@/components/ActivaCamara";
import Menu from "@/components/Menu";
import Aviso from "@/components/Aviso";
// ES6 Modules or TypeScript
import Swal from 'sweetalert2'
export default {
    name: "Login",
    components: {
        ActivaCamaras,
        Menu,
        Aviso
    },
    data() {
        return {
            titulo: "PROYECTO ACCIÓN",
            modo:''
        };
    },
    methods:{
        inicioSesion(){
          //this.$vuetify.theme.dark=false;
          this.$router.push('/tiempo-real');
        },
        recuperarContrasena(){
            Swal.fire({
                title: 'Introduce el correo electronico de recuperación',
                input: 'text',
                inputAttributes: {
                    autocapitalize: 'off'
                },
                showCancelButton: true,
                confirmButtonText: 'Enviar',
                showLoaderOnConfirm: true
            }).then((result) => {
                if (result.isConfirmed) {
                    Swal.fire({
                        title: 'Correo electrónico enviado',
                        text: 'Se ha enviado un correo electrónico a la cuenta introducida con las instrucciones de recuperación'
                    })
                }
            })
        },
    }
}
</script>

<style lang="scss" scoped>
.cuadroLogin {
  margin: 3rem auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  min-width: 350px;
  max-width: 100%;
  background: #f0f0f0;
  border-radius: 5px;
  padding: 40px;
  box-shadow: 0 4px 10px 4px rgba(0, 0, 0, 0.3);
}

.cuadroLoginOscuro {
  margin: 3rem auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  min-width: 350px;
  max-width: 100%;
  background: #505050;
  border-radius: 5px;
  padding: 40px;
  box-shadow: 0 4px 10px 4px rgba(0, 0, 0, 0.3);
}

.form_label {
  margin-top: 2rem;
  color: black;
  margin-bottom: 0.5rem;
  &:first-of-type {
    margin-top: 0rem;
  }
}

.form_label_oscuro {
  margin-top: 2rem;
  color: #ffffff;
  margin-bottom: 0.5rem;
  &:first-of-type {
    margin-top: 0rem;
  }
}

.form_input {
  padding: 10px 15px;
  background: none;
  background-image: none;
  border: 1px solid black;
  color: black;
  &:focus {
    outline: 0;
    border-color: #197BF6;
  }
}

.form_input_oscuro {
  padding: 10px 15px;
  background: none;
  background-image: none;
  border: 1px solid #ffffff;
  color: #ffffff;
  ::placeholder { color: blue; }
  &:focus {
    outline: 0;
    border-color: #197BF6;
  }
}

.form_input_oscuro::placeholder {
  color: #ffffff;
}

.form-submit {
  background:#197BF6;
  border: none;
  color: white;
  margin-top: 3rem;
  padding: 1rem 0;
  
  cursor: pointer;
  transition: background 0.2s;
  &:hover {
    background: #0b9185;
  }
}
</style>