<template>
  <div>
    <Menu :titulo="titulo" />

    <div class="camaras" v-if="pag == 1">
      <ActivaCamaras :imagen="'/habitacion.jpg'" :estado="estado" :nombre="'salón'" v-for="(i, key) in 9" :pos="i" :key="key" />
    </div>
    <div class="camaras" v-else>
      <ActivaCamaras :imagen="'/habitacion2.jpg'" :estado="estado" :nombre="'habitación'" v-for="(i, key) in 9" :pos="i" :key="key" />
    </div>

    <div class="camaras">
      <button  class="boton-pag" @click="pag--" v-if="pag != 1">Anterior</button>
      <button class="boton-pag" @click="pag = 1">1</button>
      <button class="boton-pag" @click="pag = 2">2</button>
      <button class="boton-pag" @click="pag++" v-if="pag != 2">Siguiente</button>
    </div>
    <!-- Boton de apagar el circuito de camaras  -->
    <div>
      <button class="boton-apagar" @click="apagar"> {{camara}}</button>
    </div>

    <!-- Boton de aviso a emergencias  -->
    <Aviso/>
  </div>
</template>

<script>
import ActivaCamaras from "@/components/ActivaCamara";
import Menu from "@/components/Menu";
import Aviso from "@/components/Aviso";

import Swal from 'sweetalert2'

export default {
  name: "ControlCamaras",
  components: {
    ActivaCamaras,
    Menu,
    Aviso
  },
  data() {
    return {
      titulo: "GESTIÓN DE CÁMARAS",
      pag: 1,
      estado:true,
      camara: "Apagar circuito"
    };
  },
  methods: {
    apagar(){
      if(this.camara == "Apagar circuito"){
        Swal.fire({
            title: '¿Esta seguro de apagar todo el circuito de cámaras?',
            text:'Si apaga el circuito completo no podra observar ningún movimiento de las cámaras',
            showCancelButton: true,
            confirmButtonText: 'Si',
            cancelButtonText: 'No',
            showLoaderOnConfirm: true
        }).then((result) => {
            if (result.isConfirmed) {
              this.estado = false;
              this.camara = "Encender circuito";
            }
        })
      }
      else{
        this.estado = true;
        this.camara = "Apagar circuito";
      }
      
    }
  }
};
</script>

<style>
h1 {
  padding-left: 5%;
  border-bottom: 2px solid #197BF6;
}

.boton-apagar {
  background-color: #197BF6;
  font-size: 1.5em;
  color: white;
  height: 60px;
  width: 15%;
  border: none;
  border-radius: 10px;
  position: fixed;
  bottom: 10px;
  right: 20px;
}
.camaras{
    text-align: center;
}

.boton-pag{
  color:#197BF6;
  font-size: 1.2em;
  margin-top: 20px;
  margin-left:20px;
  margin-right: 20px;
}
</style>
