<template>
  <div :class="{cuadritos: !this.$vuetify.theme.dark, cuadritosOscuros: this.$vuetify.theme.dark}">
    <img src="@/assets/habitacion.jpg" alt="Imagen de la camara" />
    <div class="datos">
      <p>Grabación {{ pos }}</p>
      <div style="width:100%;">
        <div :class="{datos_grabaciones: !this.$vuetify.theme.dark, datos_grabaciones_oscuro: this.$vuetify.theme.dark}">
          Fecha de realización: {{ video.fecha }}
          <br>
          Duración: {{ video.duracion }}
          <br>
          Tamaño del archivo: {{ video.tamaño}}
          <br>
        </div>

        <div class="opciones">
          <button><div style="color:white;">Reproducir</div></button>
        </div>
        <div class="opciones">
          <button v-on:click="descargar()"><div style="color:white;">Descargar</div></button>
        </div>
        <div class="opciones">
          <button v-on:click="eliminar()"><div style="color:white;">Eliminar</div></button>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import Swal from 'sweetalert2'
export default {
  props: {
    pos: Number
  },
  data() {
    return {
      switch1: true,
      videos:[
        {
          tamaño:'275 MB',
          duracion: '09:27',
          fecha:'15/03/2020',
        },
        {
          tamaño:'495 MB',
          duracion: '18:47',
          fecha:'02/05/2020',
        },
        {
          tamaño:'55 MB',
          duracion: '01:22',
          fecha:'15/11/2020',
        },
      ],
      video:{
          tamaño:'495 MB',
          duracion: '18:47',
          fecha:'02/05/2020',
      }
    }
  },
  methods:{
        eliminar(){
          Swal.fire({
                title: '¿Deseas eliminar la grabación? No se podrá deshacer',
                showCancelButton: false,
                confirmButtonText: 'Eliminar',
                showLoaderOnConfirm: true
            }).then((result) => {
                if (result.isConfirmed) {
                  this.$store.state.numGrabaciones= this.$store.state.numGrabaciones-1
                    Swal.fire({
                        title: 'Grabación eliminada',
                        text: 'El vídeo se ha eliminado de la base de datos'
                    })
                }
            })
        },
        descargar(){
          Swal.fire({
              title: 'La descarga comenzará en breves',
              text: 'En caso de que no comience, vuelva a pulsar el botón de descarga'
          })
        },
    }
};
</script>

