  border-radius: 15px;
  background: #505050;
  display: inline-block;
  margin-bottom: 15px;
  margin-top: 10px;
}

.cuadritos:hover{
    transform: scale(1.02);
}

.cuadritosOscuros:hover{
    transform: scale(1.02);
}

.cuadritos img {
  padding: 20px 0px 10px 10px;
  width: 200px;
  height: 155px;
}

.cuadritosOscuros img {
  padding: 20px 0px 10px 10px;
  width: 200px;
  height: 155px;
}

.cuadritos .datos {
  display: inline-block;
  margin-left: 20px;
  padding-bottom: 10px;
  height: 100px;
}

.cuadritosOscuros .datos {
  display: inline-block;
  margin-left: 20px;
  padding-bottom: 10px;
  height: 100px;
}

.cuadritos .datos p {
  width: 295px;
