  line-height: 36px;
  text-align: center;
}

.datos .enlace {
  width: 15%;
  background:white;
  border-radius: 20px;
  color: blue;
  border: blue 2px solid;
  padding: 5px 15px 5px 15px;
  text-decoration: none;
}

.datos .enlace:hover {
  background: blue;
  color: white;
}
.boton {
  float:left;
  background:#197BF6;
  border: none;
  color: white;
  
  vertical-align: middle;
  margin-bottom: 15px;
}

