<template>
    <div>
      <v-app-bar color="#197BF6"
      dense
      height=60px
       class="mx-auto overflow-hidden"
      >
      
        <v-app-bar-nav-icon @click.stop="drawer = !drawer" v-if="titulo != 'PROYECTO ACCIÓN'"></v-app-bar-nav-icon>

        <v-toolbar-title class="titulo"> {{ titulo }}</v-toolbar-title>

        <v-spacer></v-spacer>
        <div style="cursor:pointer" name="boton-estado" @click="cambio()">
            <font-awesome-icon icon="cloud-sun" style="cursor:pointer;font-size: 2em;"/>
            {{ modo }}
        </div>
        </v-app-bar>
    <v-navigation-drawer v-if="titulo != 'PROYECTO ACCIÓN'" v-model="drawer" absolute temporary>
        <v-list nav dense>
            <v-list-item-group v-model="group" active-class="blue lighten-4--text ">
            <v-list-item>
                <button class="enlace" v-on:click="tiempoReal()">Cámaras en tiempo real</button>
            </v-list-item>

            <v-list-item>
                <button class="enlace" v-on:click="controlCamaras()">Gestión del circuito</button>
            </v-list-item>

            <v-list-item>
                <button class="enlace" v-on:click="misGrabaciones()">Mis grabaciones</button>
            </v-list-item>

            <v-list-item>
                <button class="enlace" v-on:click="reportar()">Repórtanos errores</button>
            </v-list-item>

            <v-list-item>
                <button class="enlace" v-on:click="cerrarSesion()">Cerrar sesión</button>
            </v-list-item>

            </v-list-item-group>
            <template v-slot:append>
                <v-btn block>
                Logout
                </v-btn>
            </template>
        </v-list>

       
          
        </v-navigation-drawer>
    </div>
</template>

<script>
import Swal from 'sweetalert2'
export default {
    props:{
        titulo:{
            default:"TIEMPO REAL",
            type: String
        }
    },
    data: () => ({
      drawer: false,
      group: null,
      modo:'',
      tam: screen.availHeight
    }),
    mounted(){
        if(this.$vuetify.theme.dark == true){
            this.modo='Modo claro'
        }else{
            this.modo='Modo oscuro'
        }
    },
    methods:{
        cambio(){
            if(this.$vuetify.theme.dark == true){
                this.modo='Modo oscuro'
                this.$vuetify.theme.dark = false
            }else{
                this.modo='Modo claro'
                this.$vuetify.theme.dark = true
            }
        },
        tiempoReal(){
            this.$router.push("/tiempo-real");
        },
        controlCamaras(){
            this.$router.push("/control-camaras");
        },
        misGrabaciones(){
            this.$router.push("/mis-grabaciones");
        },
        cerrarSesion(){
            this.$router.push("/");
        },
        reportar(){
            Swal.mixin({
                input: 'text',
                confirmButtonText: 'Next &rarr;',
                showCancelButton: true,
                progressSteps: ['1', '2']
                }).queue([
                {
                    title: 'Introduce tu correo electrónico',
                    text: 'Te contactaremos si necesitamos más información sobre el incidente'
                },
                {
                    title: 'Describa el incidente que se ha producido en la aplicación',
                    input: 'textarea'
                }
                ]).then((result) => {
                    if (result.value) {
                        const answers = JSON.stringify(result.value)
                        Swal.fire({
                            title: 'Reporte recibido correctamente',
                            text: 'Nos pondremos a solucionarlo en la mayor brevedad posible',
                            confirmButtonText: 'Aceptar'
                        })
                    }
                })
        }
    },
    watch: {
      group () {
        this.drawer = false
      },
    },
}
</script>

<style>
.titulo{
    margin-left: 10px;
    font-weight: bolder;
    font-size: 1.9em !important;
    color: white;
}

.v-icon{
    color: white !important;
    font-weight: bolder !important;
    font-size: 1.9em !important;
}


.enlace{
    text-decoration: none;
}

</style>