<template>
  <div :class="{camara_clara: !this.$vuetify.theme.dark, camara_oscura: this.$vuetify.theme.dark}">
    <div>
      <p>Cámara de {{nombre}} {{ pos }}</p>
    </div>
    <div class="contenedor-img">
      
      <img :src="imagen" alt="Imagen de la camara" />
    </div>
    <div class="datos">
      
      <div class="boton">
        <v-switch
          v-model="estado"
          inset
          :label="getEtiqueta()"
        ></v-switch>

      </div>
      <div class="boton">
        <input type="button" @click="grabar" class="grabar" />
        <p>Grabar</p> 
      </div>
    </div>
  </div>
</template>

<script>
import Swal from "sweetalert2"
export default {
  props: {
    nombre:String,
    pos: Number,
    imagen: String,
    estado: Boolean,
  },
  data() {
    return {
      etiqueta: "Encendido",
    }
  },
  methods: {
    grabar(){
        Swal.fire({
            title: '¿Quieres programar la grabación de la cámara?',
            text: "Si no quieres programarla, pulsa grabar para comenzar.",
            showDenyButton: true,
            showCancelButton: true,
            
            confirmButtonText: 'Si',
            cancelButtonText: 'Cancelar',
            denyButtonText:"Grabar",
            showLoaderOnConfirm: true
        }).then((result) => {
            if (result.isConfirmed) {
                Swal.fire({
                    title: 'Hora de inicio de la grabación',
                    text:"Recuerda que el tiempo mÁximo de grabación son 8 horas",
                    html:'<input type="time"/>',
                    showCancelButton: true,
                    confirmButtonText: 'Enviar',
                    showLoaderOnConfirm: true
                })
            }
        })
    },
    getEtiqueta(){
      if(this.estado){
          return "Encendido"
        }
        else{
          return "Apagado"
        }
    }
  },
};
</script>

