<template>
  <div class="aviso">
      <button class="boton-redondo" @click="avisar">
         <div class="intermedio">
             <div class="letra">SOS</div> 
         </div>
      </button>
  </div>
</template>

<script>
import Swal from "sweetalert2"
export default {
    methods: {
        avisar(){
            Swal.fire({
                title: '¿Esta seguro de avisar a emergencias?',
                text: "Si avisas, no hay vuelta atras.",
                showCancelButton: true,
                confirmButtonText: 'Avisar',
                cancelButtonText: "Cancelar",
                showLoaderOnConfirm: true
            }).then((result) => {
                if (result.isConfirmed) {
                    Swal.fire({
                        title: 'AVISO ENVIADO',
                        text: 'En poco tiempo contactaran con usted los servicios de emergencias'
                    })
                }
            })
        }
    },
}
</script>

<style>

.aviso{
    /* margin-left: 50px;
    margin-bottom: 20px;
    margin-top: -70px; */
    position: fixed;
    bottom: 10px;
    left: 5px;
}

.boton-redondo{
    background: yellow;
    border-radius: 50%;
    height: 120px;
    width: 120px;
    border: black 2px solid;
}

.intermedio{
    background: red;
    border-radius: 50%;
    height: 80px;
    width: 80px; 
    margin-left: auto;
    margin-right: auto;
    border: black 2px solid;
    color: white;
    font-weight: bolder;
    font-size: 1.5em;
    text-align: center;
}

.letra{
    /* margin-top: 20px;
    margin-bottom: 20px; */
    line-height: 80px;
}
</style>