import Vue from "vue";
import Login from "../views/Login.vue";
import VueRouter from "vue-router";
import ControlCamaras from "../views/ControlCamaras.vue";
import TiempoReal from "../views/TiempoReal.vue";
import MisGrabaciones from "../views/MisGrabaciones.vue";

Vue.use(VueRouter);

const routes = [{
        path: "/tiempo-real",
        name: "TiempoReal",
        component: TiempoReal
    },
    {
        path: "/control-camaras",
        name: "ControlCamaras",
        component: ControlCamaras
    },{
        path: "/",
        name: "Login",
        component: Login
    },{
        path: "/mis-grabaciones",
        name: "MisGrabaciones",
        component: MisGrabaciones
    },
];

const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes
});

export default router;