<template>
  <div class="grabar">
      <button class="boton-redondo" @click="grabar">
             <div class="letra">GRABAR</div> 
      </button>
  </div>
</template>

<script>
import Swal from "sweetalert2"
export default {
     methods: {
        grabar(){
            Swal.fire({
                title: '¿Quieres programar la grabación de la cámara?',
                text: "Si no quieres programarla, pulsa grabar para comenzar.",
                showDenyButton: true,
                showCancelButton: true,
                
                confirmButtonText: 'Si',
                cancelButtonText: 'Cancelar',
                denyButtonText:"Grabar",
                showLoaderOnConfirm: true
            }).then((result) => {
                if (result.isConfirmed) {
                    Swal.fire({
                        title: 'Hora de inicio de la grabación',
                        text:"Recuerda que el tiempo máximo de grabación son 8 horas",
                        html:'<input type="time"/>',
                        showCancelButton: true,
                        confirmButtonText: 'Enviar',
                        showLoaderOnConfirm: true
                    })
                }
            })
        }
  }
}
</script>

<style scoped>

.grabar{
    position: fixed;
    bottom: 10px;
    right: 5px;
}

.boton-redondo{
    background: red;
    border-radius: 50%;
    height: 120px;
    width: 120px;
    border: black 2px solid;
}

.intermedio{
    background: red;
    border-radius: 50%;
    height: 80px;
    width: 80px; 
    margin-left: auto;
    margin-right: auto;
    border: black 2px solid;
    color: white;
    font-weight: bolder;
    font-size: 1.5em;
    text-align: center;
}

.letra{
    color: white;
    font-weight: bold;
    /* margin-top: 20px;
    margin-bottom: 20px; */
    line-height: 80px;
}
</style>